"use client";

import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { ArrowLeft, ChevronLeft, ChevronRight, Menu, MessageSquare, Settings, Heart } from "lucide-react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { useUserStore } from "@/store/useUserStore";

export default function ReaderPage({ params }: { params: { mangaId: string; chapterId: string } }) {
  const router = useRouter();
  const { unlockChapter, isUnlocked, patinhas } = useUserStore();
  
  // Estado da UI (Mostrar/Esconder menus)
  const [showUI, setShowUI] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);

  // Simulação de Imagens do Capítulo (Webtoon fatiado)
  // Em um app real, isso viria de uma API/CDN
  const pages = [
    "https://images.unsplash.com/photo-1518709268805-4e9042af9f23?q=80&w=600&auto=format&fit=crop", // Imagem 1
    "https://images.unsplash.com/photo-1531512073830-ba890ca4eba2?q=80&w=600&auto=format&fit=crop", // Imagem 2
    "https://images.unsplash.com/photo-1605806616949-1e87b487bc2a?q=80&w=600&auto=format&fit=crop", // Imagem 3
    "https://images.unsplash.com/photo-1507608616759-54f48f0af0ee?q=80&w=600&auto=format&fit=crop", // Imagem 4
  ];

  const currentChapter = parseInt(params.chapterId);
  const nextChapterId = currentChapter + 1;

  // Lógica de "Smart UI": Esconder menus ao rolar para baixo, mostrar ao rolar para cima
  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      
      if (currentScrollY > lastScrollY && currentScrollY > 100) {
        setShowUI(false); // Rolando para baixo -> Esconde
      } else {
        setShowUI(true); // Rolando para cima -> Mostra
      }
      setLastScrollY(currentScrollY);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [lastScrollY]);

  // Função para ir ao próximo capítulo (com verificação de pagamento)
  const handleNextChapter = () => {
    // Verifica se precisa pagar pelo próximo
    // (Lógica simplificada: caps > 3 pagam)
    if (nextChapterId > 3 && !isUnlocked(params.mangaId, nextChapterId)) {
        const confirmBuy = window.confirm(`O Capítulo ${nextChapterId} custa 1 Patinha. Deseja desbloquear?`);
        if (confirmBuy) {
            const success = unlockChapter(params.mangaId, nextChapterId, 1);
            if (success) {
                router.push(`/leitor/${params.mangaId}/${nextChapterId}`);
            } else {
                alert("Saldo insuficiente!");
            }
        }
    } else {
        // Se for grátis ou já comprado
        router.push(`/leitor/${params.mangaId}/${nextChapterId}`);
    }
  };

  return (
    <div className="min-h-screen bg-[#111] text-white">
      
      {/* --- HEADER FLUTUANTE --- */}
      <AnimatePresence>
        {showUI && (
          <motion.header
            initial={{ y: -100 }}
            animate={{ y: 0 }}
            exit={{ y: -100 }}
            className="fixed top-0 left-0 right-0 z-50 bg-black/80 backdrop-blur-md border-b border-white/10 h-16 flex items-center justify-between px-4"
          >
            <div className="flex items-center gap-4">
              <Link href={`/obra/${params.mangaId}`} className="p-2 hover:bg-white/10 rounded-full transition-colors">
                <ArrowLeft className="w-6 h-6" />
              </Link>
              <div>
                <h1 className="text-sm font-bold text-gato-ghost uppercase tracking-wider">Solo Leveling</h1>
                <p className="text-xs text-gato-purple font-bold">Capítulo {params.chapterId}</p>
              </div>
            </div>

            <div className="flex items-center gap-2">
               {/* Menu de Capítulos (Dropdown simplificado) */}
              <button className="hidden md:flex items-center gap-1 bg-white/10 px-3 py-1 rounded-full text-xs hover:bg-white/20">
                <Menu className="w-3 h-3" /> Lista
              </button>
              <button className="p-2 hover:bg-white/10 rounded-full">
                <Settings className="w-5 h-5" />
              </button>
            </div>
          </motion.header>
        )}
      </AnimatePresence>

      {/* --- ÁREA DE LEITURA (IMAGENS) --- */}
      {/* Clicar no meio da tela alterna a UI */}
      <div 
        onClick={() => setShowUI(!showUI)} 
        className="w-full max-w-3xl mx-auto flex flex-col items-center bg-black min-h-screen pt-0 md:pt-0"
      >
        {pages.map((src, index) => (
          <img 
            key={index}
            src={src}
            alt={`Página ${index + 1}`}
            className="w-full h-auto block select-none pointer-events-none"
            loading="lazy"
            style={{ display: 'block' }} // Garante que não haja espaço extra
          />
        ))}

        {/* --- RODAPÉ DO CAPÍTULO (INTERAÇÃO) --- */}
        <div className="w-full bg-gato-gray/20 p-8 text-center space-y-6 mt-4 pb-32">
            <h3 className="text-gato-muted text-sm uppercase tracking-widest">Fim do Capítulo</h3>
            
            {/* Ações Sociais */}
            <div className="flex justify-center gap-4">
                <button className="flex items-center gap-2 bg-white/5 px-4 py-2 rounded-full text-sm hover:bg-red-500/20 hover:text-red-400 transition-colors">
                    <Heart className="w-4 h-4" /> Curtir
                </button>
                <button className="flex items-center gap-2 bg-white/5 px-4 py-2 rounded-full text-sm hover:bg-blue-500/20 hover:text-blue-400 transition-colors">
                    <MessageSquare className="w-4 h-4" /> Comentários (128)
                </button>
            </div>

            {/* Botões de Navegação */}
            <div className="flex items-center justify-between max-w-sm mx-auto pt-4">
                <button 
                    onClick={() => {
                        const prev = currentChapter - 1;
                        if (prev > 0) router.push(`/leitor/${params.mangaId}/${prev}`);
                    }}
                    disabled={currentChapter <= 1}
                    className="flex items-center gap-2 text-gato-muted hover:text-white disabled:opacity-30 disabled:cursor-not-allowed"
                >
                    <ChevronLeft className="w-5 h-5" /> Anterior
                </button>

                <button 
                    onClick={handleNextChapter}
                    className="flex items-center gap-2 bg-gato-purple hover:bg-gato-purple/80 text-white px-8 py-3 rounded-full font-bold shadow-[0_0_15px_#8A2BE2] transition-all hover:scale-105 active:scale-95"
                >
                    Próximo <ChevronRight className="w-5 h-5" />
                </button>
            </div>
        </div>
      </div>

      {/* --- FOOTER FLUTUANTE (BARRA DE PROGRESSO) --- */}
      <AnimatePresence>
        {showUI && (
          <motion.footer
            initial={{ y: 100 }}
            animate={{ y: 0 }}
            exit={{ y: 100 }}
            className="fixed bottom-0 left-0 right-0 z-50 bg-black/90 backdrop-blur-md border-t border-white/10 px-6 py-4"
          >
             <div className="max-w-3xl mx-auto flex items-center gap-4">
                {/* Botão Anterior Mini */}
                <button className="text-gato-muted hover:text-white">
                    <ChevronLeft className="w-6 h-6" />
                </button>

                {/* Barra de Progresso (Visual) */}
                <div className="flex-1 h-1 bg-white/10 rounded-full overflow-hidden">
                    <div className="h-full bg-gato-purple w-3/4 rounded-full" />
                </div>
                <span className="text-xs text-gato-muted">Pag 18/24</span>

                {/* Botão Próximo Mini */}
                <button onClick={handleNextChapter} className="text-gato-muted hover:text-white">
                    <ChevronRight className="w-6 h-6" />
                </button>
             </div>
          </motion.footer>
        )}
      </AnimatePresence>

    </div>
  );
}